.div {
  position: fixed;
  right: 5%;
  bottom: 5%;
  z-index: 1;
}
.btn {
  font-weight: bold;
  font-size: 15px;
  padding: 15px 10px;
  background-color: black;
  color: #fff;
  border: 1px solid white;
  border-radius: 100%;
  outline: none;
  cursor: pointer;
}
.btn:hover {
  color: black;
  background-color: white;
}
