.loginform {
  top: 50%;
  max-height: 30%;
  width: 30%;
  padding: 30px;
  border: 2px solid black;
  left: 50%;
  position: absolute;
  display: flex;
  flex-flow: column;
  justify-content: center;
  transform: translate(-50%, -50%);
}
